body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    background-color: #eff3f7;
    height: 100%;
}

html, body, #root, .container {
    height: 100%;
    display: flex;
    justify-content: center;
}

.loader {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.movies {
    display: flex;
    width: 80%;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    padding: 20px;
    padding-top: 70px;
}

.movies .movie {
    width: 45%;
    display: flex;
    background-color: #fff;
    margin-bottom: 70px;
    align-items: flex-start;
    justify-content: space-between;
    font-weight: 300;
    color: rgba(133, 136, 134, 0.781);
    box-shadow: 3px 5px 14px #0006;
}

.movie img {
    position: relative;
    top: -50px;
    max-width: 180px;
    width: 100%;
    margin-right: 30px;
}

.movie .movie__title,
.movie .movie__year {
    margin: 0;
    font-weight: 300;
}

.movie .movie__title {
    margin-top: 5px;
    font-size: 24px;
}

.movie .movie__title,
.movie .movie__genres {
    color: #313131;
}

.movie .movie__genres {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    margin: 5px 0px;
}

.movie .movie__year {
    font-size: 14px;
}
.movie__genres li{
    margin-right: 10px;
    font-size: 10px;
}

